import React , {createContext , useState , useEffect}from 'react';
import Axios from 'axios';
export const myData = createContext();
const DataContextProvider = (props) => {
    const 
        [header ,setHeader] = useState([]), 
        [circle ,setCircle] = useState([]),
        [square ,setSquare] = useState([]), 
        [goalsTitel ,setGoalsTitel] = useState(''), 
        [activitiesTitel ,setActivitiesTitel] = useState(''), 
        [activitiesSubTitel ,setActivitiesSubTitel] = useState(''), 
        [activitiesContent ,setActivitiesContent] = useState([]), 
        [activitiesButton ,setActivitiesButton] = useState(''), 
        [newsTitel ,setNewsTitel] = useState(''), 
        [newsContent ,setNewsContent] = useState([]), 
        [newsButton ,setNewsButton] = useState(''), 
        [videosTitel ,setVideosTitel] = useState(''), 
        [videosContent ,setVideosContent] = useState([]), 
        [videosButton ,setVideosButton] = useState(''), 
        [sponsers ,setSponsers] = useState([]),
        [id ,setId] = useState(0);
    const respond= ()=>{
        Axios.get('APIS/main.json').then((res , index)=>{
            const data = res.data;
            setHeader([...data.header]);
            setCircle([...data.circle]);
            setSquare([...data.square]);
            setGoalsTitel(data.goalsTitel);
            setActivitiesTitel(data.activitiesTitel);
            setActivitiesSubTitel(data.activitiesSubTitel);
            setActivitiesContent([...data.activitiesContent]);
            setActivitiesButton(data.activitiesButton);
            setNewsTitel(data.newsTitel);
            setNewsContent([...data.newsContent]);
            setNewsButton(data.newsButton);
            setVideosTitel(data.videosTitel);
            setVideosContent([...data.videosContent]);
            setVideosButton(data.videosButton);
            setSponsers([...data.sponsers]);
            setId(index)
        });
    }
    useEffect(()=>{
        respond();
    } , [id])    
    return ( 
        <myData.Provider value={{
            header,
            circle,
            square,
            goalsTitel,
            activitiesTitel,
            activitiesSubTitel,
            activitiesContent,
            activitiesButton,
            newsTitel,
            newsContent,
            newsButton,
            videosTitel,
            videosContent,
            videosButton,
            sponsers,
        }}>
            {props.children}
        </myData.Provider>
    );
}
 
export default DataContextProvider;