/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext } from 'react';
import './style.scss';
import OwlCarousel from 'react-owl-carousel2';
import Rotate from 'react-reveal/Rotate';
import {myData} from '../Contexts/DataContext'
const Sponsers = () => {
  const options = {
    rtl:true,
    loop:true,
    margin:10,
    items:8,
    autoplay:true,
    responsive:{
      0:{
        items:4,

      },
      768:{
        items:5
      },
    },
  }  
  const {sponsers} = useContext(myData);
  let i = 2000;
  const content = () =>{
    let contentHandel;
    if (window.innerWidth > 991) {
      contentHandel = sponsers.map(res =>(
        <Rotate key={res.id} top right delay={res.id === 1 ? i : i -= 200 }>
          <a href="#"><img src={res.img} alt="Sponser img" /></a>
        </Rotate>
      ));
    }else{
      if (sponsers.length !==0) {
        contentHandel = (
          <OwlCarousel options={options}>
            {
              sponsers.map(res =>(
                <div key={res.id}>
                  <a href="#"><img src={res.img} alt="Sponser img" /></a>
                </div>
              ))
            }
          </OwlCarousel>
        )
      }else{
        return null;
      }
    }
    return contentHandel;
  }

  return (
    <section className="sponsers-section">
      <div className="container">
        <div className="sponsers-containrt">
          {content()}
        </div>
      </div>
    </section>
  )
  
}
export default Sponsers;