import React , {useContext}  from 'react';
import {Link } from 'react-router-dom';
import './style.scss';
import Bounce from 'react-reveal/Bounce';
import Rotate from 'react-reveal/Rotate';
import {myData} from '../Contexts/DataContext'
const News = (props) =>{ 
  const {newsTitel , newsContent , newsButton } = useContext(myData);
  const contentHandel = newsContent.map(res =>(
    
    <div  className="col-lg-6" key={res.id}>
      <Bounce bottom delay={res.id === 1 ? 400 : 800}>
        <div className="card-con">
          <div className="img-box">
            <img src={res.img} alt="news img"/>
            <div className="date">
              <span>{res.dateNum}</span>
              <span>{res.date}</span>
            </div>
          </div>
          <div className="caption">
            <h3>{res.titel}</h3>
            <p>{res.body}</p>
          </div>
        </div>
      </Bounce>
    </div>

  ))
  return(
      <section className="news-section">
        <div className="container">
          <Rotate bottom right>
            <h2>{newsTitel}</h2>
          </Rotate>
          <div className="row">
            {contentHandel}
          </div>
          <div className="main-btn">
          <Bounce bottom>
              <Link to="/news">
                <span>{newsButton}</span>
              </Link>
            </Bounce>
          </div>
        </div>
      </section>
    )
}
export default News;
