import React , {useState , useEffect , useContext} from 'react';
import './style.scss';
import {Link } from 'react-router-dom';
import OwlCarousel from 'react-owl-carousel2';
import classnames from "classnames";
import Rotate from 'react-reveal/Rotate';
import Bounce from 'react-reveal/Bounce';
import {myData} from '../Contexts/DataContext'

const Videos = () => {
  const options = {
    rtl:true,
    loop:true,
    margin:20,
    items:1,
    autoplay:true,
    center: true,
    responsive:{
      0:{
          stagePadding: 0,
      },
      768:{
          stagePadding: 200,
      },
      992:{
        stagePadding: 350,
      },
      1200:{
        stagePadding: 340,
      },
      1500:{
        stagePadding: 550,

      }

    },
  };
  const [container,setContainer] = useState(false);
  const windowWidth = () =>{
    window.innerWidth < 480 ? setContainer(true) : setContainer(false) ;
  }
  useEffect(()=>{
    windowWidth();
  })
  
  const {videosTitel , videosContent , videosButton } = useContext(myData);
  const contentHandel = videosContent.map(res =>(
    <div className="item" key={res.id} id={res.id}>
      <a  data-fancybox="youtube" href={res.youtube} className="fans">
        <img src={res.img} alt="slide img" className="img-fluid" />
      </a>
    </div>
  ));
  if (videosContent.length === 0) {
    return null;
  }else{
    return(
      <section className="Videos-section">
        <div className={classnames({"container":container})}>
          <Rotate top right>
            <h2 className="titel">{videosTitel}</h2>
          </Rotate>
            <OwlCarousel options={options}>
              {contentHandel}
            </OwlCarousel>
            <div className="main-btn">
              <Bounce bottom>
                <Link to="/vidues">
                  <span>{videosButton}</span>
                </Link>
              </Bounce>
            </div>
        </div>
      </section>
    )    
  }
}
export default Videos;
