import React , { useState }from 'react';
import Rotate from 'react-reveal/Rotate';
import Bounce from 'react-reveal/Bounce';
import './style.scss';
import Scroll from '../ScrollToTop';
import Axios from 'axios';
import { useAlert } from "react-alert";

const Contact = () =>{
    const [name , steName] = useState('');
    const [phone ,setPhone ] = useState('');
    const [email ,setEmail] = useState();
    const [massage ,setMassage] = useState();
    const alert = useAlert();
    
    const postDataHandelar = () =>{
        if (name !=='' && phone !=='' && email !=='') {
            const Data = {
                name,
                phone,
                email,
                massage
            }
            Axios.post('http://5dd67df8ce4c3000144039cd.mockapi.io/contact' , Data).then(res=>{
                steName('');
                setPhone('');
                setEmail('');
                setMassage('');

            });            
        }else{
            alert.show(' من فضلك املأ جميع البيانات ')
        }
    }
    return(
        <section className="contact-section">
            <Scroll />
            <div className="container">
                <Rotate top right mountOnEnter>
                    <h2>يسعدنا دائما تواصلكم ومشاركتنا الأنشطة التوعوية</h2>
                </Rotate>
                <div className="row">
                    <div className="col-lg-6">
                        <Bounce right cascade>
                            <form>
                                <div>
                                    <label htmlFor="name">الاسم بالكامل<span>*</span></label>
                                    <input type="text" id="name" required value={name} onChange={e=>steName(e.target.value)}/>
                                </div>
                                <div>
                                    <label htmlFor="phone">رقم الجوال<span>*</span></label>
                                    <input type="number" id="phone" required value={phone} onChange={e=>setPhone(e.target.value)}/>
                                </div>
                                <div>
                                    <label htmlFor="email">البريد الإلكتروني<span>*</span></label>
                                    <input type="email" id="email" required  value={email} onChange={e=>setEmail(e.target.value)}/>
                                </div>
                                <div>
                                    <label htmlFor="massage">نص الرسالة<span> (لا يقل عن 100 حرف) </span></label>
                                    <textarea id="massage" required value={massage} onChange={e=>setMassage(e.target.value)}/>
                                    <button type="submit" onClick={(e)=>{postDataHandelar(); e.preventDefault()}}>إرسال</button>
                                </div>
                            </form>
                        </Bounce>

                    </div>
                    <div className="col-lg-6">
                        <Bounce left cascade>

                            <div className="content">
                                <div className="info">
                                    <i className="fas fa-home"></i>

                                    <p> 
                                        مستشفى الملك فيصل التخصصي ومركز الابحاث
                                        <span className="bar"><span>الرياض:</span> 11211 <span>- ص.ب:</span> 3354 - بريد داخلي: 03</span>
                                    </p>
                                </div>
                                <div className="info">
                                    <i className="fas fa-phone"></i>

                                    <p> 
                                        <span>الرقم الرئيسي:</span>
                                        +966-11-4647272
                                        <span className="bar"><span>تحويلة:</span> 31907 “إلغاء رقم مسؤول الحملة”</span>
                                    </p>
                                </div>
                                <div className="info">
                                    <i className="fas fa-mobile-alt"></i>

                                    <p> 
                                        +966-504399441
                                    </p>
                                </div>
                                <div className="info">
                                    <i className="far fa-envelope"></i>
                                    <p> gcccp@kfshrc.edu.sa</p>
                                </div>
                            </div>
                        </Bounce>


                    </div>
                </div>
            </div>
        </section>
    )

}
export default Contact ;