import React ,{useContext} from 'react';
import './style.scss';
import Rotate from 'react-reveal/Rotate';
import Flip from 'react-reveal/Flip';
import Bounce from 'react-reveal/Bounce';
import {myData} from '../Contexts/DataContext'
const Goals = () =>{
  const {circle , square , goalsTitel } = useContext(myData);
  const circleHandel = circle.map(circleItems =>(
      <Flip key={circleItems.id} left delay={circleItems.id === 1 ? 400 : circleItems.id === 2 ? 800 : 1200}>
        <div className="con">
          <div className="circle">
            <p>
              {
                circleItems.body.map((res , index)=>(<span key={index}>{res}</span>))
              }
            </p>
          </div>
        </div>
      </Flip >
  ));    
  const squareHandel = square.map(squareItems =>(
    <div className="square" key={squareItems.id}>
      <div className="num"><span>+</span>{squareItems.count}</div>
        <p>
          {
            squareItems.body.map((res , index)=>(<span key={index}>{res}</span>))
          }
        </p>
    </div>
  ));
  return(  
    <section className="goals-section" id="goals">
      <div className="container">
        <Rotate bottom right>
          <h2>{goalsTitel}</h2>
        </Rotate>
          <div className="content-top">
            {circleHandel}
          </div>
        <Bounce bottom cascade>
          <div className="content-botton">
            {squareHandel}
          </div>
        </Bounce>
      </div>
    </section>
  )
  
}
export default Goals;