import React ,{useContext} from 'react';
import './style.scss';
import Zoom from 'react-reveal/Zoom';
import Rotate from 'react-reveal/Rotate';
import RubberBand from 'react-reveal/RubberBand';
import Tilt from 'react-tilt';
import Scroll from '../ScrollToTop';
import {myData} from '../Contexts/DataContext'
const Header = () =>{ 
    const {header} = useContext(myData);
    
    const headerHandel = header.map((res, index) =>(
      <header className="main-header" key={index}>
        <Scroll />
          <img src={res.patrn} className="patrn" alt="patrn"/>
          <a href="#goals" className="scroll-header">
            <span></span>
          </a>
          <div className="container">
            <div className="row">
              <div className="col-lg-6">
                <div className="content">
                  <h1>
                  <Rotate top right mountOnEnter>
                    <span>{res.titelAR}</span>
                  </Rotate>
                  <Zoom left cascade mountOnEnter>
                    <span>{res.titelEN}</span>
                  </Zoom>
                  </h1>
                </div>
              </div>
              <div className="col-lg-6">
                <RubberBand mountOnEnter>
                  <div className="vidue">
                    <Tilt options={{ scale:1 , max:10}} >
                      <a  data-fancybox="youtube" href={res.youtube} className="fans">
                        <img src={res.img} className="img-fluid" alt="slide img" />
                      </a>
                    </Tilt>
                  </div>
              </RubberBand>
            </div>
          </div>
        </div>
    </header>
  ))
  return headerHandel;
}
export default Header;
