import React ,{Fragment , useContext} from 'react';
import {Link } from 'react-router-dom';
import './style.scss';
import Rotate from 'react-reveal/Rotate';
import Slide from 'react-reveal/Slide'; 
import Tilt from 'react-tilt';
import {myData} from '../Contexts/DataContext'
const Activities = (props) =>{ 
    const {activitiesTitel, activitiesSubTitel , activitiesContent , activitiesButton } = useContext(myData);
    const contentHandel = activitiesContent.map(res =>{
      if (res.id === 2) {
        return(
          <div className="row" key={res.id}>
            <div className="col-lg-6 ">
              <div className="act-titel center">
                <Slide bottom>
                  <h2>{res.id === 1 ? res.body :<Fragment>{res.body}<span> {res.subBody}</span></Fragment>}</h2>
                </Slide>
              </div>
            </div>
            <div className="col-lg-6">
              <Tilt options={{ scale:1 , max:10 }} >
                <div className="image-con center">
                  <Slide bottom>
                    <img src={res.img} alt="activities img" className="img-fluid"/>
                  </Slide>
                </div>
              </Tilt>
            </div>
          </div>
        )
      }else{
        return(
          <div className="row" key={res.id}>
            <div className="col-lg-6  order-lg-1 order-2">
              <Tilt options={{ scale:1 , max:10 }} >
                <div className="image-con">
                  <Slide bottom delay={400}>
                    <img src={res.img} alt="activities img" className="img-fluid"/>
                  </Slide>
                </div>
              </Tilt>
            </div>
            <div className="col-lg-6 order-lg-2 order-1">
              <div className="act-titel">
                <Slide bottom offsetTop={5000}>
                  <h2>{res.id === 1 ? res.body :<Fragment>{res.body}<span> {res.subBody}</span></Fragment>}</h2>
                </Slide>
              </div>
            </div>
          </div>
      )
    }
  });
  return(
    <section className="activities-section">
      <div className="container">
        <div className="titel">
          <Rotate bottom right  >
            <h2>{activitiesTitel}</h2>
          </Rotate>
          <Slide right delay={400}>
            <p>{activitiesSubTitel}</p>
          </Slide>
        </div>
        <div className="act-con">
          {contentHandel}
        </div>
        <div className="main-btn">
          <Slide bottom>
            <Link to="/activites">
              <span>{activitiesButton}</span>
            </Link>
          </Slide>
        </div>
      </div>
    </section>
  )
}
export default Activities;